export default {
  data () {
    return {
      goodsList: []
    }
  },
  onShow () {
    // 发请求之前，需要重置goodsList
    this.goodsList = []
    this.getGoodsList()
    wx.setTabBarBadge({
      index: 2,
      text: Object.keys(wx.getStorageSync('cart')).length.toString()
    })
  },
  onHide () {
    this.$store.commit('updateCart', this.goodsList)
  },
  methods: {
    doAccount () {
      // 商品数量为0
      if (!this.totalNum) {
        wx.showToast({
          title: '请选择商品',
          icon: 'none'
        })
        return
      }
      // 如果没登陆跳转登陆
      let token = wx.getStorageSync('token')
      if (!token) {
        wx.navigateTo({ url: '/pages/login/main' })
        return
      }
      // 跳转支付
      wx.navigateTo({ url: '/pages/pay/main' })
    },
    getGoodsList () {
      let cart = this.$store.getters.getCart
      // 如果购物车数据是空的，return
      if (!cart) {
        return
      }
      this.$request({
        url: '/api/public/v1/goods/goodslist?goods_ids=' + Object.keys(cart)
      }).then(data => {
        // goodsList和购物车数据融合
        data.forEach(v => {
          v.num = cart[v.goods_id].num
          v.checked = cart[v.goods_id].checked
        })
        this.goodsList = data
      })
    }
  },
  computed: {
    // 所有被选中的商品价格*数量
    totalPrice () {
      return this.goodsList.reduce((sum, v) => {
        return sum + (v.checked ? v.num * v.goods_price : 0)
      }, 0)
    },
    // 所有被选中的商品的数量
    totalNum () {
      // let sum = 0
      // this.goodsList.forEach(v => {
      //   if (v.checked) {
      //     sum += v.num
      //   }
      // })
      // return sum

      return this.goodsList.reduce((sum, v) => {
        return sum + (v.checked ? v.num : 0)
      }, 0)
    },
    isAll: {
      // 如果所有商品都勾选的话，就true;详解：拿出goodsList.length==勾选商品的个数
      // 默认true,遍历goodsList，如果有一项没勾选的话，就false
      // let _isAll = true
      // this.goodsList.forEach(v => {
      //   if (!v.checked) {
      //     _isAll = false
      //     // break
      //   }
      // })
      // for (let item of this.goodsList) {
      //   if (!item.checked) {
      //     _isAll = false
      //     break
      //   }
      // }
      // return _isAll
      get () {
        return this.goodsList.every(v => {
          return v.checked
        })
      },
      set (status) {
        this.goodsList.forEach(v => {
          v.checked = status
        })
      }
    }
  }
}
